import React from "react";

import styles from "./NavigationList.module.css";
import NavigationItem from "./NavigationItem/NavigationItem";

import { connect } from "react-redux";
import { logout } from "../../../store/actions/auth";

const navigationList = props => {
	let authSpecific = (
		<React.Fragment>
			<NavigationItem path="/logout" clicked={props.onLogout}>
				Logout
			</NavigationItem>
			<NavigationItem path="/history" clicked={props.clickLink}>
				Order History
			</NavigationItem>
		</React.Fragment>
	);
	if (!props.isLoggedIn) {
		authSpecific = (
			<NavigationItem path="/auth" clicked={props.clickLink}>
				Login
			</NavigationItem>
		);
	}

	return (
		<ul className={styles.navList}>
			<NavigationItem path="/build" clicked={props.clickLink}>
				Build Burger
			</NavigationItem>
			{authSpecific}
		</ul>
	);
};

const mapStateToProps = state => {
	return {
		isLoggedIn: !!state.auth.userId
	};
};

const mapDispatchToProps = dispatch => {
	return {
		onLogout: () => dispatch(logout())
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(navigationList);
