import React from "react";

const withLazyLoad = importComponent => {
	return class extends React.Component {
		state = {
			comp: null
		};

		componentDidMount() {
			importComponent().then(response => {
				this.setState({ comp: response.default });
			});
		}

		render() {
			const Comp = this.state.comp;
			return this.state.comp ? <Comp {...this.props} /> : null;
		}
	};
};

export default withLazyLoad;
