import React, { useEffect } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import styles from "./App.module.css";
import withLazyLoading from "./utilities/withLazyLoading/withLazyLoading";

import BurgerBuilder from "./containers/BurgerBuilder/BurgerBuilder";
import Toolbar from "./containers/Toolbar/Toolbar";

import { connect } from "react-redux";
import { autoLogin } from "./store/actions/auth";

const App = props => {
	useEffect(() => {
		props.attemptAutoLogin();
	}, [props]);

	const lazyAuth = withLazyLoading(() => {
		return import("./containers/Auth/Auth");
	});

	const lazyOrderHistory = withLazyLoading(() => {
		return import("./components/Order/OrderHistory/OrderHistory");
	});

	const lazyCheckout = withLazyLoading(() => {
		return import("./components/Order/Checkout/Checkout");
	});

	let authSpecificRoutes = <Route path="/auth" component={lazyAuth} />;

	if (props.isLoggedIn) {
		authSpecificRoutes = (
			<React.Fragment>
				<Route path="/checkout" component={lazyCheckout} />
				<Route path="/history" component={lazyOrderHistory} />
			</React.Fragment>
		);
	}

	return (
		<React.Fragment>
			<Toolbar />
			<main className={styles.main}>
				<Switch>
					<Route path="/build" component={BurgerBuilder} />
					{authSpecificRoutes}
					<Redirect to="/build" />
				</Switch>
			</main>
		</React.Fragment>
	);
};

const mapStateToProps = state => {
	return {
		isLoggedIn: !!state.auth.userId
	};
};

const mapDispatchToProps = dispatch => {
	return {
		attemptAutoLogin: () => dispatch(autoLogin())
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
