import {
	AUTH_START,
	AUTH_SUCCESS,
	AUTH_FAIL,
	LOGOUT
} from "../../vars/constants";
import axios from "axios";

const authStart = () => {
	return {
		type: AUTH_START
	};
};

const authSuccess = authData => {
	return {
		type: AUTH_SUCCESS,
		userId: authData.localId,
		token: authData.idToken
	};
};

const authFail = error => {
	return {
		type: AUTH_FAIL,
		error: error
	};
};

export const logout = () => {
	localStorage.removeItem("token");
	localStorage.removeItem("expirationTime");
	localStorage.removeItem("userId");
	return {
		type: LOGOUT
	};
};

const checkAuthTimeout = time => {
	return dispatch => {
		setTimeout(dispatch(logout()), time * 1000);
	};
};

export const auth = (email, password, isNewUser) => {
	return dispatch => {
		dispatch(authStart());
		const authData = {
			email: email,
			password: password,
			returnSecureToken: true
		};
		let url =
			"https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyCAZp2wgDLhGj20Cjx4S3EH_-vmOysJfRU";
		if (isNewUser) {
			url =
				"https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyCAZp2wgDLhGj20Cjx4S3EH_-vmOysJfRU";
		}
		axios
			.post(url, authData)
			.then(response => {
				const expirationTime =
					Date.now() + parseInt(response.data.expiresIn) * 1000;
				localStorage.setItem("token", response.data.idToken);
				localStorage.setItem("expirationTime", expirationTime);
				localStorage.setItem("userId", response.data.localId);

				checkAuthTimeout(response.data.expiresIn);
				dispatch(authSuccess(response.data));
			})
			.catch(error => {
				dispatch(authFail(error));
			});
	};
};

export const autoLogin = () => {
	return dispatch => {
		const token = localStorage.getItem("token");
		const expTime = parseInt(localStorage.getItem("expirationTime"));
		if (token && expTime > Date.now()) {
			const userId = localStorage.getItem("userId");
			dispatch(authSuccess({ localId: userId, idToken: token }));
			checkAuthTimeout((expTime - Date.now()) / 1000);
		} else if (token && expTime <= Date.now()) {
			dispatch(logout());
		}
	};
};
