import {
	PURCHASE,
	AJAX_START,
	AJAX_FAIL,
	ORDER_INPROGRESS,
	RESET_ORDER,
	FETCH_ORDERS_SUCCESS
} from "../../vars/constants";

import axiosOrder from "../../ajax/axios-orders";

const ajaxStart = () => {
	return {
		type: AJAX_START
	};
};

const ajaxFail = error => {
	return {
		type: AJAX_FAIL,
		error: error
	};
};

const purchase = (order, id) => {
	return {
		type: PURCHASE,
		order: order,
		id: id
	};
};

export const attemptPurchase = (order, token) => {
	return dispatch => {
		dispatch(ajaxStart());
		axiosOrder
			.post("/orders.json?auth=" + token, order)
			.then(response => {
				const id = response.data.name;
				dispatch(purchase(order, id));
			})
			.catch(error => {
				console.log(error.message);
				dispatch(ajaxFail());
			});
	};
};

export const orderInprogress = () => {
	return {
		type: ORDER_INPROGRESS
	};
};

export const resetOrder = () => {
	return {
		type: RESET_ORDER
	};
};

const fetchOrdersSuccess = orders => {
	return { type: FETCH_ORDERS_SUCCESS, orders: orders };
};

export const fetchOrders = (token, userId) => {
	return dispatch => {
		dispatch(ajaxStart());
		const queryParams = `?auth=${token}&orderBy="userId"&equalTo="${userId}"`;
		axiosOrder
			.get("/orders.json" + queryParams)
			.then(response => {
				dispatch(fetchOrdersSuccess(response.data));
			})
			.catch(error => {
				dispatch(ajaxFail(error));
			});
	};
};
