import {
	PURCHASE,
	AJAX_FAIL,
	ORDER_INPROGRESS,
	RESET_ORDER,
	AJAX_START,
	FETCH_ORDERS_SUCCESS
} from "../../vars/constants";

const initialState = {
	orders: null,
	hasError: false,
	loading: false,
	orderInprogress: false
};

const reducer = (state = initialState, action) => {
	const newState = { ...state };
	switch (action.type) {
		case ORDER_INPROGRESS:
			newState.orderInprogress = true;
			break;
		case RESET_ORDER:
			newState.orderInprogress = false;
			break;
		case AJAX_START:
			newState.loading = true;
			break;
		case AJAX_FAIL:
			newState.hasError = true;
			newState.loading = false;
			break;
		case PURCHASE:
			newState.loading = false;
			newState.orderInprogress = false;
			break;
		case FETCH_ORDERS_SUCCESS:
			newState.loading = false;
			newState.orders = action.orders;
		//no default
	}
	return newState;
};

export default reducer;
