import {
	AUTH_START,
	AUTH_SUCCESS,
	AUTH_FAIL,
	LOGOUT
} from "../../vars/constants";

const initialState = {
	loading: false,
	error: null,
	userId: null,
	token: null
};

const reducer = (state = initialState, action) => {
	switch (action.type) {
		case AUTH_START:
			return {
				...state,
				loading: true
			};
		case AUTH_SUCCESS:
			return {
				...state,
				loading: false,
				userId: action.userId,
				token: action.token,
				error: null
			};
		case AUTH_FAIL:
			return {
				...state,
				loading: false,
				error: action.error
			};
		case LOGOUT:
			return {
				...state,
				userId: null,
				token: null
			};
		default:
			return state;
	}
};

export default reducer;
